h1, h2, h3, h4, h5, h6, p {
  margin: 0;
}

p:last-child {
  margin-bottom: 0;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

button {
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

:root {
  --main-orange: #f87719;
  --primary-black: #111;
  --secondary-black: #282828;
  --neutral-gray: #595959;
  --light-neutral-gray: #b7b7b7;
  --off-white: #f8f8f8;
  --pure-white: #fff;
  --gradient-main: linear-gradient(141.22deg, #ffc226 9.4%, #f84119 91.91%);
  --gradient-reverse: linear-gradient( 141.22deg, #f84119 9.4%, rgba(248, 159, 25, .68) 91.91% );
  --transition-duration: 250ms;
  --transition-curve: cubic-bezier(.4, 0, .2, 1);
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: currentColor;
  text-decoration: none;
}

.header {
  width: 100%;
  z-index: 50;
  background-color: var(--primary-black);
  position: relative;
  top: 0;
  left: 0;
}

body.light-theme .header {
  background-color: var(--pure-white);
  transition: background-color var(--transition-curve) var(--transition-duration);
}

.header-container {
  margin: 0 auto;
  padding: 0 32px;
}

.header-body {
  height: 60px;
  justify-content: space-between;
  align-items: center;
  display: flex;
  position: relative;
}

.header-logo-link {
  align-items: center;
  gap: 8px;
  display: flex;
}

.header-logo-link img {
  z-index: 4;
  position: relative;
}

body.light-theme .header-span {
  color: var(--primary-black);
}

.header-span {
  z-index: 2;
  transition: color var(--transition-curve) var(--transition-duration);
  color: var(--pure-white);
  font-family: Roboto;
  font-size: 24px;
  font-style: normal;
  font-weight: 400;
  line-height: 28px;
  display: block;
}

.header-mobile-menu {
  display: none;
}

.header-list {
  gap: 40px;
  display: flex;
}

.header-link a:hover {
  color: var(--main-orange);
  transition: color var(--transition-curve) var(--transition-duration);
}

.header-switcher {
  z-index: 2;
  position: relative;
}

.header-theme-switcher {
  width: 60px;
  height: 24px;
  -ms-appearance: none;
  appearance: none;
  background: var(--gradient-main);
  transition: var(--transition-curve) var(--transition-duration);
  border-radius: 20px;
  outline: none;
}

.header-theme-switcher:before {
  content: "";
  width: 17px;
  height: 17px;
  background: var(--pure-white);
  transition: var(--transition-curve) var(--transition-duration);
  border-radius: 20px;
  position: absolute;
  top: 4px;
  left: 6px;
  transform: scale(1.1);
}

.header-theme-switcher:checked:before {
  left: 40px;
}

.sun {
  pointer-events: none;
  position: absolute;
  top: 4px;
  left: 40px;
}

.half-moon {
  pointer-events: none;
  position: absolute;
  top: 4px;
  left: 6px;
}

@media screen and (max-width: 768px) {
  .header-mobile-menu {
    z-index: 2;
    display: block;
  }

  .header-container {
    margin: 0 auto;
    padding: 0 15px;
  }

  .header-mobile-menu button {
    color: var(--neutral-gray);
    text-transform: uppercase;
    transition: color var(--transition-curve) var(--transition-duration);
    font-family: Roboto;
    font-size: 12px;
    font-style: normal;
    font-weight: 500;
    line-height: 14px;
  }

  .header-mobile-menu button:focus {
    color: var(--main-orange);
    transition: color var(--transition-curve) var(--transition-duration);
  }

  .header-logo-link img {
    width: 32px;
    height: 32px;
  }

  .header-span {
    display: none;
  }

  .header-body {
    height: 48px;
  }

  .header-menu {
    width: 204px;
    height: 100%;
    background-color: var(--primary-black);
    z-index: 3;
    transition: var(--transition-curve) var(--transition-duration);
    border-radius: 0 10px 10px 0;
    padding: 80px 0 0;
    position: fixed;
    top: 0;
    left: -500px;
  }

  body.light-theme .header-menu {
    background-color: var(--off-white);
  }

  .header-list {
    display: block;
  }

  .header-link {
    text-transform: uppercase;
    padding: 0 0 24px 30px;
    font-family: Roboto;
    font-size: 14px;
    font-style: normal;
    font-weight: 400;
    line-height: 16px;
  }

  body.light-theme .header-link {
    color: var(--primary-black);
    font-weight: 600;
  }

  .header-link a:focus {
    color: var(--main-orange);
    transition: color var(--transition-curve) var(--transition-duration);
  }

  .header-menu.active {
    transition: var(--transition-curve) var(--transition-duration);
    left: 0;
  }

  .header-theme-switcher {
    z-index: 2;
  }

  body.lock {
    overflow: hidden;
  }
}

@media screen and (max-width: 1280px) {
  body.light-theme .header-span {
    color: var(--secondary-black);
  }

  .header-span:hover, :focus {
    color: var(--main-orange);
    transition: color var(--transition-curve) var(--transition-duration);
  }

  .header-menu {
    z-index: 2;
  }

  body.light-theme .header-link {
    color: var(--neutral-gray);
  }

  .header-link:hover, :focus {
    color: var(--main-orange);
    transition: color var(--transition-curve) var(--transition-duration);
  }

  .header-theme-switcher {
    z-index: 2;
  }
}

.header-link {
  text-transform: uppercase;
  color: var(--neutral-gray);
  transition: color var(--transition-curve) var(--transition-duration);
  font-family: Roboto;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.index-page-body .header-link-index, .catalog-page-body .header-link-catalog, .library-page-body .header-link-library {
  color: var(--main-orange);
}

.header-menu-btn {
  cursor: pointer;
  background: none;
  border: none;
  margin: 0;
  padding: 0;
}

