#movies-list {
  flex-direction: row;
  justify-content: space-between;
  margin: 24px 20px;
  display: flex;
}

.movie-card {
  width: 280px;
  height: 406px;
  border-radius: 5px;
  flex-basis: calc(33.333% - 24px);
  margin-bottom: 16px;
  position: relative;
}

.movie-card:after {
  content: "";
  width: 100%;
  height: 100%;
  z-index: 1;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 85.16%);
  position: absolute;
  top: 0;
  left: 0;
}

.movie-card img {
  width: 280px;
  height: 406px;
  background: linear-gradient(rgba(0, 0, 0, 0) 63.48%, rgba(0, 0, 0, .9) 92.16%);
}

.movie-title {
  text-align: left;
  color: #fff;
  text-transform: uppercase;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, .7);
  font-family: Roboto;
  font-size: 13px;
  font-weight: bold;
  line-height: 20px;
}

.movie-info {
  text-align: left;
  color: #fff;
  text-shadow: 1px 1px 3px rgba(0, 0, 0, .7);
  z-index: 2;
  flex-direction: column;
  font-family: Roboto;
  font-size: 13px;
  font-weight: 500;
  line-height: 20px;
  display: flex;
  position: absolute;
  top: 88%;
  left: 5%;
}

.movie-info p {
  color: #b7b7b7;
  font-size: 12px;
}

body.light-theme .movie-info {
  color: #111;
}

.stars {
  color: #f87719;
  flex-direction: row;
  padding: .87px .44px;
  display: flex;
  position: absolute;
  bottom: 0;
  left: 190px;
}

.movie-container {
  width: 280px;
  height: 406px;
  overflow-y: overlay;
  flex-wrap: wrap;
  justify-content: space-between;
  gap: 16px;
  margin-left: 20px;
  margin-right: 20px;
  display: block;
